<template>
  <div class="DetailsPage">
    <!-- 商品信息 -->
    <div class="goodsmeg tabs">
      <!-- 头部导航栏 -->
      <van-nav-bar left-arrow :border="false" class="navBar" @click-left="back">
        <template #title>
          <ul v-show="isShow">
            <li
              v-for="(item, i) in tabslist"
              :key="i"
              :class="{ active: scrTop == item.offheight }"
              @click="getScroll(item.offheight)"
            >
              {{ item.title }}
            </li>
          </ul>
        </template>
        <template #right>
          <van-icon name="cart-o" @click="goShoppCart" />
        </template>
      </van-nav-bar>

      <!-- 商品图片 -->
      <van-swipe @change="onChange" :loop="false">
        <van-swipe-item v-for="(image, index) in imgs" :key="index">
          <img v-lazy="image" />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">
            {{ current + 1 }}/{{ imgs.length }}
          </div>
        </template>
      </van-swipe>

      <!-- 商品信息 -->
      <div class="goodsinfo">
        <!-- 价格 -->
        <van-cell :border="false">
          <template #title>
            <span class="price"><span>￥</span>{{ storeInfo.price }}</span>
            <span class="sub">￥{{ storeInfo.ot_price }}</span>
          </template>
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <i class="iconfont iconhanhan-01-011" @click="isShowShare"></i>
          </template>

          <!-- 分享弹出层 -->
          <van-share-sheet
            v-model="showShare"
            title="立即分享给好友"
            :options="options"
          />
        </van-cell>

        <!-- 标题 -->
        <p class="title">{{ storeInfo.store_name }}</p>

        <!-- 库存和月销 -->
        <div class="sub">
          <span class="stock">库存 {{ storeInfo.stock }}</span>
          <span class="ficti">月销 {{ storeInfo.ficti }}</span>
        </div>
      </div>

      <!-- 选择规格 -->
      <div class="productAttr" @click="select">
        <van-cell is-link>
          <!-- 使用 title 插槽来自定义标题 -->
          <template #title>
            <span class="title">选择</span>
            <span class="sub">{{
              currentspecifi == "" ? "请选择..." : "已选择" + currentspecifi
            }}</span>
          </template>
        </van-cell>
      </div>

      <!-- 商品规格弹出层 -->
      <van-popup
        v-model="showPopup"
        closeable
        close-icon="close"
        position="bottom"
        class="goodsPopup"
      >
        <div class="header">
          <div class="img-wrap"><img :src="storeInfo.image" /></div>
          <div class="main">
            <span class="price">￥{{ storeInfo.price }}</span>
            <span class="stock">库存 {{ storeInfo.stock }} 件</span>
            <span class="select">
              <span v-show="currentspecifi == ''"
                >请选择{{ productAttr | filterAttr }}</span
              >
              <span v-show="currentspecifi != ''"
                >已选择{{ currentspecifi }}</span
              >
            </span>
          </div>
        </div>

        <ul class="productAttrs">
          <li v-for="(item, i) in productAttr" :key="i">
            <p>{{ item.attr_name }}</p>
            <div class="attr">
              <span
                v-for="(val, k) in item.attr_value"
                :key="k"
                :class="{ active: item.type == k }"
                @click="mark(i, k)"
                >{{ val.attr }}</span
              >
            </div>
          </li>
          <li class="buy">
            <p>购买数量</p>
            <span>限购5件</span>
            <van-stepper v-model="goodsNum" max="5" disable-input />
          </li>
        </ul>

        <van-button
          class="sure"
          round
          block
          v-show="isAdd"
          @click="addShoopCart"
          >确定加入购物车</van-button
        >
        <van-button class="sure" round block v-show="isBuy" @click="buyNow"
          >确定购买</van-button
        >
        <van-goods-action v-show="isSelect">
          <van-goods-action-button
            color="#ff8600"
            type="warning"
            text="加入购物车"
            @click="addShoopCart"
          />
          <van-goods-action-button
            color="#ff0036"
            type="danger"
            text="立即购买"
          />
        </van-goods-action>
      </van-popup>
    </div>

    <!-- 商品评价 -->
    <div class="evaluate tabs">
      <van-cell
        is-link
        title="商品评价(0)"
        value="查看全部"
        :to="`/evaluate/${storeInfo.id}`"
      ></van-cell>
    </div>

    <!-- 商品推荐 -->
    <div class="recommend tabs">
      <div class="superior">
        <img src="../assets/image/icon.png" />
        <span>优品推荐</span>
        <img src="../assets/image/icon.png" />
      </div>
      <van-swipe class="my-swipe" indicator-color="white">
        <van-swipe-item class="swipeone"
          ><swipeList :List="good_list.swipeone"
        /></van-swipe-item>
        <van-swipe-item class="swipetwo"
          ><swipeList :List="good_list.swipetwo"
        /></van-swipe-item>
        <van-swipe-item class="swipethree"
          ><swipeList :List="good_list.swipethree"
        /></van-swipe-item>
      </van-swipe>
    </div>

    <!-- 商品详情 -->
    <div class="particulars tabs">
      <p class="title">产品介绍</p>
      <div class="content" ref="content"></div>
    </div>

    <!-- 商品底部导航栏 -->
    <van-goods-action>
      <!-- <van-goods-action-icon icon="service-o" text="客服" /> -->
      <!-- <van-goods-action-icon icon="shop-o" text="店铺" /> -->
      <van-goods-action-icon icon="cart-o" text="购物车" @click="goShoppCart" />
      <van-goods-action-icon
        :icon="storeInfo.userCollect ? 'like' : 'like-o'"
        :text="storeInfo.userCollect ? '已收藏' : '收藏'"
        :color="storeInfo.userCollect ? '#ff8500' : 'inherit'"
        @click="collectGoods"
      />

      <van-goods-action-button
        color="#ff8600"
        type="warning"
        text="加入购物车"
        @click="addSelect"
      />
      <van-goods-action-button
        color="#ff0036"
        type="danger"
        text="立即购买"
        @click="buySelect"
      />
    </van-goods-action>
  </div>
</template>

<script>
import "../assets/style/Details.less";
import swipeList from "../components/MinigoodsList";
export default {
  name: "Details", //商品详情页
  props: ["ids"],
  components: {
    swipeList,
  },
  data() {
    return {
      // tab栏数据
      tabslist: [
        {
          title: "商品",
          offheight: "",
        },
        {
          title: "评价",
          offheight: "",
        },
        {
          title: "推荐",
          offheight: "",
        },
        {
          title: "详情",
          offheight: "",
        },
      ],

      //商品图片
      imgs: [],

      // 商品详情
      storeInfo: {},

      // 商品规格
      productAttr: [],

      // 商品规格组合
      productValue: [],

      // 推荐商品数据
      good_list: {},

      //当前图片下标
      current: 0,

      //是否显示tab栏
      isShow: false,

      //导航栏元素
      navBar: "",

      // 页面滚入的距离
      scrTop: "",

      // 是否弹出商品规格层
      showPopup: false,

      isAdd: false, //是否是添加购物车

      isBuy: false, //是否是购买

      isSelect: false, //是否是选择规格

      currentspecifi: "", //当前选择的规格

      unique: "", //商品规格id

      goodsNum: 1, //商品初始数量

      showShare: false, //是否显示分享

      // 分享弹框数据
      options: [
        [
          { name: "微信", icon: require("../assets/image/weixin.png") },
          { name: "朋友圈", icon: require("../assets/image/pengyouquan.png") },
          { name: "微博", icon: require("../assets/image/weibo.png") },
          { name: "QQ", icon: require("../assets/image/QQ.png") },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },
  created() {
    document.documentElement.scrollTop = 0;

    this.getGoodsDatas();
  },
  mounted() {
    this.navBar = document.querySelector(".navBar"); //获取导航栏元素

    setTimeout(() => {
      this.getheight(); // 获取每个tabs距离顶部的距离

      window.addEventListener("scroll", this.handleScroll); //监听页面滚动
    }, 1000);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  watch: {
    ids(id) {
      this.getGoodsDatas();
      console.log(id);
    },
  },
  methods: {
    // 返回上一级路由
    back() {
      this.$router.back();
    },

    //获取商品数据
    getGoodsDatas() {
      let tokenString = this.$cookies.get("tokenString");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      // 请求商品详情数据
      this.axios({
        method:"get",
        url:this.baseUrl + "/product/detail/" + this.ids,
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((data) => {
          // 获取商品信息
          this.storeInfo = data.data.data.storeInfo;

          // 获取商品规格
          data.data.data.productAttr.forEach((item) => {
            item.type = 999;
          });
          this.productAttr = data.data.data.productAttr;

          // 获取商品规格
          this.productValue = data.data.data.productValue;

          // 获取商品规格组合
          this.good_list = {
            swipeone: data.data.data.good_list.slice(0, 6),
            swipetwo: data.data.data.good_list.slice(6, 12),
            swipethree: data.data.data.good_list.slice(12, 18),
          };

          // 获取商品图片banner
          this.imgs = data.data.data.storeInfo.slider_image;

          this.$refs.content.innerHTML = this.storeInfo.description;

          this.$toast.clear();
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    // 显示分享弹出层
    isShowShare() {
      this.showShare = true;
    },

    //设置当前图片的下标
    onChange(index) {
      this.current = index;
    },

    // 获取给个tabs距离顶部的距离
    getheight() {
      let el = document.querySelectorAll(".tabs");
      for (let i = 0; i < el.length; i++) {
        this.tabslist[i].offheight = el[i].offsetTop;
      }
    },

    //页面滚入方法
    handleScroll() {
      let navheight = this.navBar.offsetHeight; //获取导航栏的高度
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; // 获取滑动距离
      this.scrTop = scrollTop;

      if (scrollTop < navheight) {
        this.navBar.classList.remove("transparent");
        this.isShow = false;
      } else if (
        navheight <= scrollTop &&
        scrollTop < this.tabslist[1].offheight - navheight
      ) {
        this.navBar.classList.add("transparent");
        this.isShow = true;
        this.scrTop = this.tabslist[0].offheight;
      } else if (
        this.tabslist[1].offheight - navheight <= scrollTop &&
        scrollTop < this.tabslist[2].offheight - navheight
      ) {
        this.scrTop = this.tabslist[1].offheight;
      } else if (
        this.tabslist[2].offheight - navheight <= scrollTop &&
        scrollTop < this.tabslist[3].offheight - navheight
      ) {
        this.scrTop = this.tabslist[2].offheight;
      } else if (this.tabslist[3].offheight - navheight <= scrollTop) {
        this.scrTop = this.tabslist[3].offheight;
      }
    },

    //实现锚点的效果
    getScroll(h) {
      let navheight = this.navBar.offsetHeight; //获取导航栏的高度
      document.documentElement.scrollTop = h - navheight;
    },

    // 标记选择的规格
    mark(i1, i2) {
      this.productAttr[i1].type = i2;

      this.currentspecifi = "";
      this.productAttr.forEach((item) => {
        if (item.type != 999) {
          this.currentspecifi += item.attr_value[item.type].attr + ",";
        }
      });
      this.currentspecifi = this.currentspecifi.slice(0, -1);
    },

    //选择规格
    select() {
      this.isSelect = true;
      this.showPopup = true;
      this.isAdd = false;
      this.isBuy = false;
    },

    //点击加入购物车选规格
    addSelect() {
      this.showPopup = true;
      this.isSelect = false;
      this.isAdd = true; //可以添加购物车
      this.isBuy = false; //不购买
    },

    // 点击购买选规格
    buySelect() {
      this.showPopup = true;
      this.isSelect = false;
      this.isAdd = false; //不添加购物车
      this.isBuy = true; //可以购买
    },

    // 添加购物车
    addShoopCart() {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      for (const key in this.productValue) {
        if (key == this.currentspecifi) {
          this.unique = this.productValue[key].unique;
        }
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/cart/add",
        data: {
          cartNum: this.goodsNum,
          new: 0,
          productId: this.storeInfo.id,
          uniqueId: this.unique ? this.unique : "",
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then((res) => {
        console.log(res);
        this.$toast.clear();

        //没登陆的话 跳到登录页面
        if (res.data.status == 410000) {
          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
          return this.$router.push({ name: "Login" });
        }

        //登陆了，就提示
        if (res.data.status === 200) {
          this.$toast({
            message: "加入成功",
            forbidClick: true,
            duration: 3000,
          });

          if(this.isBuy){
            this.$router.push({name: 'BuyNow', params: {id: res.data.data.cartId}})
          }

        } else {
          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
        }
      });

      this.showPopup = false;
    },

    //收藏/取消收藏商品
    collectGoods() {
      // 判断token存不存在
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.storeInfo.userCollect
          ? this.baseUrl + "/collect/del"
          : this.baseUrl + "/collect/add",
        data: {
          category: "product",
          id: this.storeInfo.id,
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          console.log("res getDetailData ==> ", res);
          this.$toast.clear();

          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status === 200) {
            this.$toast({
              message: this.storeInfo.userCollect ? "取消收藏" : "已收藏",
              forbidClick: true,
              duration: 1000,
            });

            //已收藏/取消收藏
            this.storeInfo.userCollect = !this.storeInfo.userCollect;
            this.axios(this.baseUrl + "/product/detail/" + this.ids).then(
              (data) => {
                // 修改商品信息
                data.data.data.storeInfo.userCollect = this.storeInfo.userCollect;
              }
            );
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    // 去到购物车页面
    goShoppCart() {
      this.$router.push({ name: "Cart" });
    },

    // 立即购买
    buyNow(){

      this.addShoopCart()

    }

  },
  filters: {
    //过滤规格
    filterAttr(arr) {
      let str = "";
      for (const item of arr) {
        str += " " + item.attr_name;
      }
      return str;
    },
  },
};
</script>